'use client'

import Image from 'next/image'
import { useRef, useEffect } from 'react'
import { motion, useScroll, useTransform, useSpring, useMotionValue } from 'framer-motion'

// Orb positions (approximate center positions as percentages)
const orbPositions = [
  { x: 0.05, y: 0.25 },   // Orb 1 - top-left
  { x: 0.10, y: 0.75 },   // Orb 2 - bottom-left  
  { x: 0.85, y: 0.85 },   // Orb 3 - bottom-right
  { x: 0.90, y: 0.55 },   // Orb 4 - mid-right
  { x: 0.50, y: 0.80 },   // Orb 5 - center-bottom
]

const INFLUENCE_RADIUS = 400
const MAX_PUSH = 80

// Left column experiences
const leftColumnExperiences = [
  { logo: '/images/company-logos/meta.png', logoAlt: 'Meta', logoWidth: 86, logoHeight: 18, roles: ['Senior Product Designer'], dates: '2025 - Present' },
  { logo: '/images/company-logos/square-logo.png', logoAlt: 'Square', logoWidth: 96, logoHeight: 28, roles: ['Staff Product Designer', 'Senior Product Designer'], dates: '2025 - Present' },
  { logo: '/images/company-logos/dropbox-logo.png', logoAlt: 'Dropbox', logoWidth: 106, logoHeight: 27, roles: ['Senior Product Designer'], dates: '2025 - Present' },
]

// Right column experiences
const rightColumnExperiences = [
  { logo: '/images/company-logos/shopify-logo.png', logoAlt: 'Shopify', logoWidth: 99, logoHeight: 33, roles: ['Senior Product Designer', 'Product Designer', 'UX Design Intern'], dates: '2025 - Present' },
  { logo: '/images/company-logos/brainstation-logo.png', logoAlt: 'BrainStation', logoWidth: 147, logoHeight: 31, roles: ['UX Instructor'], dates: '2025 - Present' },
]

export default function Hero() {
  const sectionRef = useRef<HTMLElement>(null)
  
  // Motion values for each orb's offset (pushed by cursor)
  const orb1OffsetX = useMotionValue(0)
  const orb1OffsetY = useMotionValue(0)
  const orb2OffsetX = useMotionValue(0)
  const orb2OffsetY = useMotionValue(0)
  const orb3OffsetX = useMotionValue(0)
  const orb3OffsetY = useMotionValue(0)
  const orb4OffsetX = useMotionValue(0)
  const orb4OffsetY = useMotionValue(0)
  const orb5OffsetX = useMotionValue(0)
  const orb5OffsetY = useMotionValue(0)
  
  // Smooth spring animation for each orb
  const orb1SpringX = useSpring(orb1OffsetX, { stiffness: 150, damping: 20 })
  const orb1SpringY = useSpring(orb1OffsetY, { stiffness: 150, damping: 20 })
  const orb2SpringX = useSpring(orb2OffsetX, { stiffness: 120, damping: 25 })
  const orb2SpringY = useSpring(orb2OffsetY, { stiffness: 120, damping: 25 })
  const orb3SpringX = useSpring(orb3OffsetX, { stiffness: 100, damping: 20 })
  const orb3SpringY = useSpring(orb3OffsetY, { stiffness: 100, damping: 20 })
  const orb4SpringX = useSpring(orb4OffsetX, { stiffness: 130, damping: 22 })
  const orb4SpringY = useSpring(orb4OffsetY, { stiffness: 130, damping: 22 })
  const orb5SpringX = useSpring(orb5OffsetX, { stiffness: 110, damping: 18 })
  const orb5SpringY = useSpring(orb5OffsetY, { stiffness: 110, damping: 18 })
  
  const orbOffsets = [
    { x: orb1OffsetX, y: orb1OffsetY },
    { x: orb2OffsetX, y: orb2OffsetY },
    { x: orb3OffsetX, y: orb3OffsetY },
    { x: orb4OffsetX, y: orb4OffsetY },
    { x: orb5OffsetX, y: orb5OffsetY },
  ]
  
  // Track mouse movement and calculate push for each orb
  useEffect(() => {
    const handleMouseMove = (e: MouseEvent) => {
      const mouseX = e.clientX
      const mouseY = e.clientY
      const windowW = window.innerWidth
      const windowH = window.innerHeight
      
      orbPositions.forEach((orbPos, index) => {
        const orbCenterX = orbPos.x * windowW
        const orbCenterY = orbPos.y * windowH
        const deltaX = mouseX - orbCenterX
        const deltaY = mouseY - orbCenterY
        const distance = Math.sqrt(deltaX * deltaX + deltaY * deltaY)
        
        if (distance < INFLUENCE_RADIUS && distance > 0) {
          const strength = 1 - (distance / INFLUENCE_RADIUS)
          const pushStrength = strength * strength * MAX_PUSH
          const pushX = -(deltaX / distance) * pushStrength
          const pushY = -(deltaY / distance) * pushStrength
          orbOffsets[index].x.set(pushX)
          orbOffsets[index].y.set(pushY)
        } else {
          orbOffsets[index].x.set(0)
          orbOffsets[index].y.set(0)
        }
      })
    }
    
    window.addEventListener('mousemove', handleMouseMove)
    return () => window.removeEventListener('mousemove', handleMouseMove)
  }, [orbOffsets])
  
  const { scrollYProgress } = useScroll({
    target: sectionRef,
    offset: ['start start', 'end end'],
  })

  // Part 1: Tagline - visible 0-20%, fades 20-30%
  const part1Opacity = useTransform(scrollYProgress, [0, 0.15, 0.25], [1, 1, 0])
  const part1Y = useTransform(scrollYProgress, [0, 0.25], [0, -50])
  
  // Part 2: Philosophy - fades in 20-30%, visible 30-40%, fades 40-50%
  const part2Opacity = useTransform(scrollYProgress, [0.2, 0.3, 0.4, 0.5], [0, 1, 1, 0])
  const part2Y = useTransform(scrollYProgress, [0.2, 0.3, 0.4, 0.5], [30, 0, 0, -50])
  
  // Header text transition: "Elizabeth Zhu" -> "Experience"
  const elizabethOpacity = useTransform(scrollYProgress, [0.4, 0.55], [1, 0])
  const experienceOpacity = useTransform(scrollYProgress, [0.5, 0.65], [0, 1])
  
  // Image fade out
  const imageOpacity = useTransform(scrollYProgress, [0.45, 0.65], [1, 0])
  
  // Background color transition (dark to light)
  const bgOpacity = useTransform(scrollYProgress, [0.45, 0.65], [1, 0])
  
  // Orbs fade out
  const orbsOpacity = useTransform(scrollYProgress, [0.4, 0.55], [1, 0])
  
  // Experience content - fades in 55-70%
  const experienceContentOpacity = useTransform(scrollYProgress, [0.55, 0.7], [0, 1])
  const experienceContentY = useTransform(scrollYProgress, [0.55, 0.7], [30, 0])
  
  // LinkedIn footer
  const linkedinOpacity = useTransform(scrollYProgress, [0.35, 0.45], [1, 0])

  // Parallax orbs - scroll-based movement
  const orb1ScrollY = useTransform(scrollYProgress, [0, 1], [0, -150])
  const orb1ScrollX = useTransform(scrollYProgress, [0, 1], [0, 30])
  const orb2ScrollY = useTransform(scrollYProgress, [0, 1], [0, -80])
  const orb2ScrollX = useTransform(scrollYProgress, [0, 1], [0, -20])
  const orb3ScrollY = useTransform(scrollYProgress, [0, 1], [0, -200])
  const orb3ScrollX = useTransform(scrollYProgress, [0, 1], [0, 50])
  const orb4ScrollY = useTransform(scrollYProgress, [0, 1], [0, -60])
  const orb4ScrollX = useTransform(scrollYProgress, [0, 1], [0, -40])
  const orb5ScrollY = useTransform(scrollYProgress, [0, 1], [0, -120])
  const orb5ScrollX = useTransform(scrollYProgress, [0, 1], [0, 25])

  // Orb color transitions - yellows, blues, and greens only
  const orb1Color = useTransform(scrollYProgress, [0, 0.5, 1], [
    'radial-gradient(circle, rgba(16, 185, 129, 0.25) 0%, transparent 70%)',
    'radial-gradient(circle, rgba(59, 130, 246, 0.25) 0%, transparent 70%)',
    'radial-gradient(circle, rgba(251, 191, 36, 0.25) 0%, transparent 70%)'
  ])
  const orb2Color = useTransform(scrollYProgress, [0, 0.5, 1], [
    'radial-gradient(circle, rgba(251, 191, 36, 0.2) 0%, transparent 70%)',
    'radial-gradient(circle, rgba(34, 197, 94, 0.2) 0%, transparent 70%)',
    'radial-gradient(circle, rgba(96, 165, 250, 0.2) 0%, transparent 70%)'
  ])
  const orb3Color = useTransform(scrollYProgress, [0, 0.5, 1], [
    'radial-gradient(circle, rgba(14, 165, 233, 0.3) 0%, transparent 70%)',
    'radial-gradient(circle, rgba(245, 158, 11, 0.3) 0%, transparent 70%)',
    'radial-gradient(circle, rgba(16, 185, 129, 0.3) 0%, transparent 70%)'
  ])
  const orb4Color = useTransform(scrollYProgress, [0, 0.5, 1], [
    'radial-gradient(circle, rgba(74, 222, 128, 0.2) 0%, transparent 70%)',
    'radial-gradient(circle, rgba(59, 130, 246, 0.2) 0%, transparent 70%)',
    'radial-gradient(circle, rgba(252, 211, 77, 0.2) 0%, transparent 70%)'
  ])
  const orb5Color = useTransform(scrollYProgress, [0, 0.5, 1], [
    'radial-gradient(circle, rgba(59, 130, 246, 0.25) 0%, transparent 70%)',
    'radial-gradient(circle, rgba(16, 185, 129, 0.25) 0%, transparent 70%)',
    'radial-gradient(circle, rgba(251, 191, 36, 0.25) 0%, transparent 70%)'
  ])

  return (
    <section ref={sectionRef} className="relative h-[300vh]">
      {/* Light background (always present) */}
      <div className="absolute inset-0 bg-background" />
      
      {/* Dark background overlay (fades out) */}
      <motion.div 
        className="absolute inset-0"
        style={{ 
          backgroundColor: '#1A1A1A',
          opacity: bgOpacity,
        }}
      />
      
      {/* Sticky container */}
      <div className="sticky top-0 h-screen overflow-hidden">
        
        {/* Glowing orbs - background layer */}
        <motion.div 
          className="hidden lg:block absolute inset-0 z-[1] pointer-events-none overflow-hidden"
          style={{ opacity: orbsOpacity }}
        >
          <motion.div className="absolute" style={{ x: orb1ScrollX, y: orb1ScrollY, left: '-5%', top: '10%' }}>
            <motion.div style={{ x: orb1SpringX, y: orb1SpringY, width: '400px', height: '400px', background: orb1Color, filter: 'blur(60px)' }} />
          </motion.div>
          <motion.div className="absolute" style={{ x: orb2ScrollX, y: orb2ScrollY, left: '2%', top: '65%' }}>
            <motion.div style={{ x: orb2SpringX, y: orb2SpringY, width: '280px', height: '280px', background: orb2Color, filter: 'blur(50px)' }} />
          </motion.div>
        </motion.div>
        
        {/* Glowing orbs - foreground layer */}
        <motion.div 
          className="hidden lg:block absolute inset-0 z-[3] pointer-events-none overflow-hidden"
          style={{ opacity: orbsOpacity }}
        >
          <motion.div className="absolute" style={{ x: orb3ScrollX, y: orb3ScrollY, right: '10%', bottom: '10%' }}>
            <motion.div style={{ x: orb3SpringX, y: orb3SpringY, width: '400px', height: '400px', background: orb3Color, filter: 'blur(80px)' }} />
          </motion.div>
          <motion.div className="absolute" style={{ x: orb4ScrollX, y: orb4ScrollY, right: '5%', top: '50%' }}>
            <motion.div style={{ x: orb4SpringX, y: orb4SpringY, width: '300px', height: '300px', background: orb4Color, filter: 'blur(60px)' }} />
          </motion.div>
          <motion.div className="absolute" style={{ x: orb5ScrollX, y: orb5ScrollY, left: '38%', bottom: '15%' }}>
            <motion.div style={{ x: orb5SpringX, y: orb5SpringY, width: '350px', height: '350px', background: orb5Color, filter: 'blur(70px)' }} />
          </motion.div>
        </motion.div>

        {/* Profile image */}
        <motion.div 
          className="hidden lg:block absolute right-0 top-0 w-[48%] h-full z-[2] overflow-hidden"
          style={{ opacity: imageOpacity }}
        >
          <div className="w-full h-full relative">
            <Image src="/images/profile.jpg" alt="Elizabeth Zhu" fill className="object-cover object-[center_25%]" priority />
            <div className="absolute inset-0 pointer-events-none" style={{ background: 'linear-gradient(to right, #1A1A1A 0%, transparent 40%)' }} />
          </div>
        </motion.div>

        {/* Content container */}
        <div className="relative z-10 h-full px-6 lg:px-[100px]">
          
          {/* Fixed Header - transitions from "Elizabeth Zhu" to "Experience" */}
          <div className="hidden lg:block pt-28 h-[180px]">
            <motion.h1 
              className="font-serif text-white text-[60px] leading-[1.1] absolute left-[100px]"
              style={{ opacity: elizabethOpacity }}
              initial={{ opacity: 0, y: 30 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.8, delay: 0.2, ease: [0.25, 0.4, 0.25, 1] }}
            >
              Elizabeth<br />Zhu
            </motion.h1>
            
            <motion.h1 
              className="font-serif text-[#1a1a1a] text-[60px] leading-[1.1]"
              style={{ opacity: experienceOpacity }}
            >
              Experience
            </motion.h1>
          </div>

          {/* Hero content - scrolling text sections (centered in remaining space) */}
          <div className="hidden lg:flex absolute inset-0 items-center justify-start pl-[100px] lg:w-[50%] lg:max-w-[608px]">
            {/* Part 1: Tagline */}
            <motion.div 
              className="absolute inset-0 flex items-center justify-start pl-[100px]"
              style={{ opacity: part1Opacity, y: part1Y }}
            >
              <p className="font-sans text-[#CDCDCD] text-[24px] leading-relaxed max-w-[500px]" style={{ fontWeight: 400 }}>
                Senior Product Designer at Meta<br />Based in New York City / Toronto
              </p>
            </motion.div>
            
            {/* Part 2: Philosophy */}
            <motion.div 
              className="absolute inset-0 flex items-center justify-start pl-[100px]"
              style={{ opacity: part2Opacity, y: part2Y }}
            >
              <p className="font-sans text-[#CDCDCD] text-[24px] leading-relaxed max-w-[500px]" style={{ fontWeight: 400 }}>
                My work is grounded in empathy and problem solving, and shaped by continuous feedback and collaboration.
              </p>
            </motion.div>
          </div>

          {/* Experience content - Two columns - fixed at bottom */}
          <motion.div 
            className="hidden lg:block absolute bottom-[97px] left-[100px] right-[100px]"
            style={{ opacity: experienceContentOpacity, y: experienceContentY }}
          >
            <div className="flex gap-[120px]">
              {/* Left Column */}
              <div className="flex-1">
                <div className="flex flex-col gap-10">
                  {leftColumnExperiences.map((exp, index) => (
                    <div key={index} className="flex justify-between items-start gap-5">
                      <div className="flex flex-col gap-2 flex-1">
                        <div className="relative" style={{ width: exp.logoWidth, height: exp.logoHeight }}>
                          <Image src={exp.logo} alt={exp.logoAlt} width={exp.logoWidth} height={exp.logoHeight} className="object-contain object-left" />
                        </div>
                        <div className="font-sans font-medium text-[#1a1b1c] text-base">
                          {exp.roles.map((role, i) => (<p key={i}>{role}</p>))}
                        </div>
                      </div>
                      <p className="font-sans font-medium text-[#1a1b1c] text-base whitespace-nowrap">{exp.dates}</p>
                    </div>
                  ))}
                </div>
              </div>

              {/* Right Column */}
              <div className="flex-1">
                <div className="flex flex-col gap-10">
                  {rightColumnExperiences.map((exp, index) => (
                    <div key={index} className="flex justify-between items-start gap-5">
                      <div className="flex flex-col gap-2 flex-1">
                        <div className="relative" style={{ width: exp.logoWidth, height: exp.logoHeight }}>
                          <Image src={exp.logo} alt={exp.logoAlt} width={exp.logoWidth} height={exp.logoHeight} className="object-contain object-left" />
                        </div>
                        <div className="font-sans font-medium text-[#1a1b1c] text-base">
                          {exp.roles.map((role, i) => (<p key={i}>{role}</p>))}
                        </div>
                      </div>
                      <p className="font-sans font-medium text-[#1a1b1c] text-base whitespace-nowrap">{exp.dates}</p>
                    </div>
                  ))}
                </div>
              </div>
            </div>
          </motion.div>

          {/* Mobile content */}
          <div className="lg:hidden flex flex-col flex-1">
            <div className="flex flex-col justify-center py-24 flex-1">
              <motion.h1 
                className="font-serif text-white text-[48px] leading-[1.1] mb-4"
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ duration: 0.6, delay: 0.2 }}
              >
                Elizabeth<br />Zhu
              </motion.h1>
              <motion.p 
                className="font-sans text-[#CDCDCD] text-[24px] leading-relaxed" 
                style={{ fontWeight: 400 }}
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ duration: 0.6, delay: 0.4 }}
              >
                Senior Product Designer at Meta<br />Based in New York City / Toronto
              </motion.p>
            </div>
            <div className="relative flex-1 min-h-[400px] -mx-6">
              <Image src="/images/profile.jpg" alt="Elizabeth Zhu" fill className="object-cover object-top" priority />
            </div>
          </div>

          {/* LinkedIn footer - fixed at bottom */}
          <motion.a 
            href="https://www.linkedin.com/in/elizabethzhu1/"
            target="_blank"
            rel="noopener noreferrer"
            className="hidden lg:block absolute bottom-[97px] left-[100px] font-sans text-[#CDCDCD] text-base hover:text-white transition-colors"
            style={{ opacity: linkedinOpacity }}
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            transition={{ duration: 0.6, delay: 0.6 }}
          >
            LinkedIn â†’
          </motion.a>
        </div>
      </div>
    </section>
  )
}
