function ProjectCard({ project, index }: { project: typeof projects[0]; index: number }) {
  const cardRef = useRef<HTMLDivElement>(null)
  
  const { scrollYProgress } = useScroll({
    target: cardRef,
    offset: ['start end', 'end start'],
  })

  // Parallax effect for the image
  const imageY = useTransform(scrollYProgress, [0, 1], [50, -50])
  
  // Opacity for entrance
  const opacity = useTransform(scrollYProgress, [0, 0.2], [0, 1])

  return (
    <motion.div
      ref={cardRef}
      className="flex flex-col lg:flex-row gap-8 lg:gap-[84px] items-start lg:items-center"
      style={{ opacity }}
    >
      {/* Left - Project Info */}
      <div className="w-full lg:w-[436px] lg:flex-shrink-0 flex flex-col gap-16 lg:gap-[126px] px-6 lg:px-0">
        {/* Header */}
        <motion.div 
          className="flex flex-col gap-2"
          initial={{ opacity: 0, y: 30 }}
          whileInView={{ opacity: 1, y: 0 }}
          viewport={{ once: true, margin: '-100px' }}
          transition={{ duration: 0.6, delay: index * 0.1 }}
        >
          <div className="relative h-[35px] mb-2" style={{ width: project.logoWidth }}>
            <Image
              src={project.logo}
              alt={project.logoAlt}
              width={project.logoWidth}
              height={project.logoHeight}
              className="object-contain object-left"
            />
          </div>
          <h3 className="font-sans font-semibold text-text-dark text-xl lg:text-[28px]">
            {project.title}
          </h3>
          <p className="font-sans font-medium text-text-dark-alt text-sm lg:text-base">
            {project.year} | {project.role}
          </p>
        </motion.div>

        {/* Details */}
        <div className="flex flex-col gap-6 lg:gap-8">
          <motion.div
            initial={{ opacity: 0, y: 20 }}
            whileInView={{ opacity: 1, y: 0 }}
            viewport={{ once: true, margin: '-50px' }}
            transition={{ duration: 0.5, delay: 0.1 }}
          >
            <p className="font-sans font-semibold text-text-dark-alt text-sm lg:text-base mb-1">
              Opportunity
            </p>
            <p className="font-sans font-medium text-text-dark-alt text-sm lg:text-base leading-relaxed">
              {project.opportunity}
            </p>
          </motion.div>
          <motion.div
            initial={{ opacity: 0, y: 20 }}
            whileInView={{ opacity: 1, y: 0 }}
            viewport={{ once: true, margin: '-50px' }}
            transition={{ duration: 0.5, delay: 0.2 }}
          >
            <p className="font-sans font-semibold text-text-dark-alt text-sm lg:text-base mb-1">
              Solution
            </p>
            <p className="font-sans font-medium text-text-dark-alt text-sm lg:text-base leading-relaxed">
              {project.solution}
            </p>
          </motion.div>
          <motion.div
            initial={{ opacity: 0, y: 20 }}
            whileInView={{ opacity: 1, y: 0 }}
            viewport={{ once: true, margin: '-50px' }}
            transition={{ duration: 0.5, delay: 0.3 }}
          >
            <p className="font-sans font-semibold text-text-dark-alt text-sm lg:text-base mb-1">
              Role
            </p>
            <p className="font-sans font-medium text-text-dark-alt text-sm lg:text-base leading-relaxed">
              {project.roleDescription}
            </p>
          </motion.div>
        </div>
      </div>

      {/* Right - Project Image with parallax - fills remaining space */}
      <motion.div 
        className="relative w-full lg:flex-1 h-[300px] sm:h-[400px] lg:h-[615px] rounded-[16px] lg:rounded-[24px] overflow-hidden"
        initial={{ opacity: 0 }}
        whileInView={{ opacity: 1 }}
        viewport={{ once: true, margin: '-100px' }}
        transition={{ duration: 0.8, ease: [0.25, 0.4, 0.25, 1] }}
      >
        <motion.div 
          className="absolute inset-[-10%] w-[120%] h-[120%]"
          style={{ y: imageY }}
        >
          <Image
            src={project.image}
            alt={project.title}
            fill
            className="object-cover"
          />
        </motion.div>
        <div className="absolute inset-0 bg-black/20" />
        
        {/* Video PiP overlay */}
        {project.video && (
          <video
            src={project.video}
            autoPlay
            loop
            muted
            playsInline
            className="absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 w-[70%] rounded-lg shadow-2xl z-10"
          />
        )}
      </motion.div>
    </motion.div>
  )
}

